.benefits {
	&__list {
		margin: 53px 0;
		font-size: 0;
		text-align: center; }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 0 20px 40px;
		text-align: center;
		width: 33.33333%;
		&-pic {
			height: 100px;
			@include vertical;
			&::after {
				content: '';
				display: inline-block;
				vertical-align: middle;
				margin: 0 auto; }
			&_tape::after {
				@include s('tape'); }
			&_peoples::after {
				@include s('peoples'); }
			&_building::after {
				@include s('building'); }
			&_vehicle::after {
				vertical-align: bottom;
				@include s('vehicle'); }
			&_handshake::after {
				@include s('handshake'); } }
		&-title {
			margin-bottom: 5px;
			font-size: 22px;
			font-weight: 700;
			line-height: 1;
			color: $black; }
		&-sub-title {
			margin-bottom: 5px;
			font-size: 16px;
			font-weight: 500;
			color: $grey; }
		&-descr {
			font-size: 16px;
			color: $grey; } }
	&_default {
		padding: 65px 0 40px; } }
